// colors
[data-dark="false"] {
  --primary: #00629B;
  --secondary: #C69214;
  --text: #182B49;
  --background: #F5F0E6;
  --background-alt: #FFFFFF;
  --light-gray: #B6B1A9;
  --gray: #747678;
  --dark-gray: #182B49;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #0795d9;
  --secondary: #075985;
  --text: #ffffff;
  --background: #181818;
  --background-alt: #1c1c1c;
  --light-gray: #404040;
  --gray: #808080;
  --dark-gray: #b0b0b0;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Lora", sans-serif;
  --heading: "Roboto", sans-serif;
  --body: "Roboto", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1rem;
  --xl: 1.5rem;
  --xxl: 2.5rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 450;
  --bold: 500;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;
}
